import Vue from 'vue'
import NuiJs from 'nui-js'
import 'nui-js/lib/theme-default/index.css'
import 'cp'
import '../../../assets/main/main.css'

Vue.use(NuiJs)
// const config = require('configModule')
Vue.config.productionTip = false
/* eslint-disable no-new */
if (window._options == null) window._options = {}
window._options['toobar_001'] = {
  data: {
    toolbar_1_message: 'Hello Vue.js!',
    toolbar_1_visible: false
  },
  methods: {
    toobar_001_btn_0: function () {
      this.toolbar_1_message = 'Hello btn_0'
    },
    toobar_001_btn_1: function () {
      this.table_001_refresh(1)
    },
    toobar_001_btn_2: function () {
      this.toolbar_1_visible = this.toolbar_1_visible ? 0 : 1
    },
    toobar_001_btn_3: function () {
      alert('toobar_001_btn_3')
    },
    toobar_001_btn_4: function () {
      alert('toobar_001_btn_4')
    }
  },
  mounted: function (key) {
    console.log('this <==' + key)
  }
}
if (window._options == null) window._options = {}
window._options['table_001'] = {
  data: {
    table_001_data: []
  },
  methods: {
    table_001_getData: function (opt) {
      let a = [
        {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        },
        {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
        },
        {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄'
        },
        {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄'
        }
      ]
      let b = [
        {
          date: '2016-05-02',
          name: '王小二',
          address: '北京市朝阳区金沙江路 1518 弄'
        },
        {
          date: '2016-05-04',
          name: '王小二',
          address: '北京市朝阳区金沙江路 1517 弄'
        },
        {
          date: '2016-05-01',
          name: '王小二',
          address: '北京市朝阳区金沙江路 1519 弄'
        },
        {
          date: '2016-05-03',
          name: '王小二',
          address: '北京市朝阳区金沙江路 1516 弄'
        }
      ]
      return (opt === 0) ? a : b
    },
    table_001_refresh: function (opt) {
      this.table_001_data = this.table_001_getData(opt)
    }
  },
  mounted: function (key) {
    // eval('this.'+ key +'_refresh(0)');
    this.table_001_refresh(0)
  }
}
var data = Object.assign({}, window._options.toobar_001.data, window._options.table_001.data)
var methods = Object.assign({}, window._options.toobar_001.methods, window._options.table_001.methods)
new Vue({
  el: '#app',
  data,
  methods,
  mounted: function () {
    for (var key in window._options) {
      window._options[key].mounted.call(this, key)
    }
  }
})



